            import * as types from './types'
            let actions = {
                 [types.add_car]:({state,commit},payload)=>{
                            let arr = state.data;
                            let temp = false;
                            let ahh= [];
                            let  res= 0;
                            if(state.data != null){
                                
                                arr.forEach(key=> {
                                    if(key._id ==payload._id){
                                        key.num++;
                                        temp=true;
                                    }});
                                arr.forEach(key=>{
                                    ahh.push(parseInt(key.num*key.price))
                                })
                                ahh.forEach(key=>{
                                    res+=parseInt(key)
                                    console.log(res);
                                })
                            }
                            if(!temp){
                                   payload.num=1;
                                arr.push(payload);
                            }
                        commit('ahhh',res)
                        localStorage.setItem('price',res);
                        commit(types.add_car,arr);
                     localStorage.setItem('car',JSON.stringify(arr));
                    },
                    [types.dec_car]:({state,commit},{id})=>{
                            
                                let arr = state.data;
                                arr.forEach((key,index)=>{
                                    if(key._id==id){
                                        arr.splice(index,1);
                                    }
                                })
                        localStorage.setItem('car',JSON.stringify(arr));
                            commit(types.dec_car,arr)
                    },
                    [types.remove_car]:({state,commit},payload)=>{

                    },
                    [types.change_car]:({state,commit},{id,num})=>{
                              
                            let arr = state.data;
                    // ? state.data :JSON.parse(localStorage.getItem('car'));
                        arr.forEach(key => {
                            if(key._id == id){
                                    key.num += num;
                            }
                            commit(types.change_car,arr);
                        });
                        localStorage.setItem('car',JSON.stringify(arr));
                     
                    },
                    hhh({state,commit},payload){
                                if(payload){
                                commit('hhh',payload)
                                }
                           
                            //备份 pirce
                    },
                    jntm({state,commit}){
                        //进行判断
                       
    
                      let arr = state.data;
                        let sum = [];
                        let res=0;
                        if(state.data != null){
                            arr.forEach(key=>{
                                sum.push(parseInt(key.num*key.price))
                            })
                           sum.forEach(key=>{
                               res+=parseInt(sum);
                           })
                        }else{
                            res=0;
                        }
                        localStorage.setItem('price',res);
                        commit('jntm',res);
                    },
                    ahhh({state,commit},payload){
                                commit('ahhh',payload)
                                //备份 price
                    }

            }
            export default actions ;