                        <h2 class="title">新品尝鲜</h2>
                        <ul>
                            <li v-for='(item) of list' :key='item._id'>
                                <router-link :to="'/detail/'+item._id +'?dataName='+ dataName">
                                    <div class="list_left">
                                       <img :src="baseUrl + item.img" alt="">
                                    </div>
                                    <div class="list_right">
                                        <p class="content">{{item.content}}</p>
                                        <p class="price"><span>￥</span>{{item.price}}<span>.00</span></p>
                                        <p class="come">进店&gt;</p>
                                    </div>
                              </router-link>                           
                            </li>
                        </ul>
                </div>
            </template>
            
        <script>
        export default {
            props:['dataName'],
            data() {
                return {
                    baseUrl:'http://47.97.26.55:3000',
                    list:[]
                }
            },
            mounted() {
                this.$axios({
                    url:'http://47.97.26.55:3000/api/home',
                   params:{
                        _limit:111
                    }
                }).then(res=>{this.list=res.data.data;
                                console.log(res.data.data);
                        })
            },
        }
        </script>

