        <template>
            <div id="search">
                <div  class="top">
                <!-- <input type="text" placeholder="商品名称" class="ui-border-tb" v-model='value' @keyup="ahh"> -->
             <div class="search">
             <input type="search" placeholder="搜索" v-model='content' @keyup="ahh">
             <img src="../assets/img/search.png" alt="" class="fdj">
            </div>
                  
    <ul class="xiuf">
            <li class="xiu" v-for='(item) of display' :key='item._id'>
               <router-link :to="'/detail/' +item._id + '?dataName='+'home'"><a class="mui-navigate-right">{{item.name}}
                   </a></router-link>
            </li>                
    </ul>
         </div> 
                <img src="../assets/icon/ss.png" alt="" v-show='!content'>
            </div>
        </template>

        <style  scoped>
      
        .xiuf{
            width: 100%;
            height: 5rem;
            position: absolute;
            top: .9rem;
        }
        li.xiu{
            display: flex;
           justify-content : space-between;
            width: 70%;text-indent: .2rem;
            height: .5rem;
            font: .25rem/2 "";
            color: #ccc;
            border-bottom: .01rem solid #ccc;
            margin-left: 10%;
        }
          
                    img{
                        width: 100%;
                        height: 2.6rem;
                        margin-left: 0rem;
                        margin-top: .2rem;
                    }
                .top{
                    height: .6rem;;
                    position: relative;
                   
                }
  .fdj{width: .3rem;height: .3rem;}
    .top input{background: #fff;height: .4rem;width:5rem;margin: 0rem .4rem .2rem .3rem;
                   text-indent: .2rem}
    .search{background: #ccc}
        </style>

        <script>
        export default {
          
            data() {
                return {
                    content:'',
                    list:'',
                    display:[],
             
                    }
                
            },
            methods: {
                 ahh(){
                                   this.display=[];
                 this.list.forEach((key) => {
                    if(key.name.indexOf(this.content) != -1 && this.content != ''){
                                // console.log(this.content,"comtent");  
                                    this.display.push(key)  
                    }
                          
                        });     
                          console.log(this.display,'display') 
                 }
            },
             mounted() {
                this.$axios({
                    url:'http://47.97.26.55:3000/api/home',
                    params:{
                        _limit:6,
                    }
                }).then(res=>{
                    // console.log(res.data.data)
                        this.list=res.data.data
                        // console.log(this.list)
                         this.$root.$data.data.bFoot=true;      
                })
                

            },
         updated(){
                               
                
                },
           }  

        </script>