        import * as types from './types'    
 let mutations= {
            [types.add_car]:(state,payload)=>{
                     
                        state.data= payload;
            },
            [types.dec_car]:(state,arr)=>{
                       state.data=arr;
            },
            [types.remove_car]:(state,payload)=>{
                   
            },
            [types.change_car]:(state,arr)=>{
                   state.data=arr;
            },
            hhh(state,payload){
                   state.data=payload
            },
            jntm(state,res){
                   state.price=res; 
            },
            ahhh(state,payload){
                   if(payload){
                     state.price=payload
                   }
                   //备份
                  
            }
            
 }

            export default mutations;
            