                    <img src="../assets/img/goback.png" alt="" class='goback'  @click="goback">
                    <img src="../assets/img/gocar.png" alt="" class='gocar'  @click="gocar">
                     
                </div>
              
                <div class="display">
                    <div class="list_left">
                        <h2>{{info.title}}</h2>
                    <img :src="baseUrl + info.img" alt="这是手基霸">
                    </div>
                    <div class="list_right">
                        <div class="top1">
                            <p class="price"><span class="q">￥</span>{{info.price}}.00<span class="money">淘金币抵扣2%</span></p>
                            <p class="big"><span class="money te">淘金币抵扣2%</span><span class="watch">查看&gt;</span></p>
                        </div>
                        
