        <script>
        export default {
            name:'Detail',
            data(){
                return{
                        info:[],
                        baseUrl:'http://47.97.26.55:3000'
                }
            },
            mounted(){
                window.id = this.$route.params.id;
                window.dataname = this.$route.query.dataName;
                    this.$axios({
                            url:`http://47.97.26.55:3000/api/${dataname}/${id}`
                              
                        }).then(res=>{console.log(res.data.data)
                                    this.info = res.data.data;
                        })   
                        this.$root.$data.data.bFoot=false;       
                },
                methods: {
                    goback(){
                        this.$router.go(-1)
                    },
                    gocar(){
                        this.$store.dispatch('add_car',this.info)
                    },
                     gobuy(){
                         this.$router.push('/buycar')
                     }
                },
        }
        </script>

