  
            <template>
  <div class="banner">
    <ul class="clearfix">
     
      <li v-for='(item,index) of list' :key='item._id'>
       <router-link :to="'/detail/'+item._id +'?dataName='+ dataName">
        <img :src="pubUrl + item.img" alt=""/>
         <div class="text-box">
          <h2>{{index+1}}丶{{item.name}}</h2>
          <p>￥{{item.price}}</p>
        </div>
         </router-link>
      </li>
      
    </ul>

  </div>
</template>
<script>

import $ from 'jquery';
import Swipe from '../assets/js/swipe.js';

export default {
     props:['dataName'],
  data(){
      return{
        list:[],
        pubUrl:'http://47.97.26.55:3000',
      
      }
  },
  mounted(){

     this.$axios({
          url:'api/banner',       
     }).then(res=>{     
       this.list=res.data.data}).then(function(){
      new Swipe($('.banner')[0],{
      auto:2000,
      continuous:true,
      stopPropation:true,
      callback:function (index,element){
        $('.banner ol li').removeClass('active');
        $('.banner ol li').eq(index).addClass('active');
      }
    });
         
     });
  },
}
</script>

<style scoped>

.banner{position: relative;overflow: hidden; z-index: 1;overflow: hidden;}

.banner ul li{width:6.4rem;float:left;position: relative;height: 4rem;overflow: hidden;}
.banner ul li img{width:100%; display:block;overflow: hidden;}
.banner ul li .text-box{top:3rem;width:100%; padding:0 0.3rem;height:1rem; position:absolute; left:0; bottom:0;
    text-align: center;
 color:#fff; background:rgba(102,051,0,0.2);}
.text-box h2{ font-size:0.3rem; font-weight:normal; margin-top:0.22rem; line-height:100%; margin-bottom:0.22rem; overflow:hidden;white-space:nowrap;text-overflow:ellipsis;}
.text-box p{ line-height:100%;}
.banner ol{position:absolute; right:0.2rem;bottom:0.2rem;}
.banner ol li{width:0.15rem;height:0.15rem; background:#5477b2; float:left; border-radius:50%; margin-right:0.08rem;}
.banner ol li.active{ background:#fff;}
.clearfix p{text-indent: .3rem ;color:#EA0000}
</style>